import vClickOutside from 'v-click-outside'
export default {
  name: 'Navbar',
  directives: {
    clickOutside: vClickOutside.directive
  },
  data() {
    return {
      scrollY: 0,
      open: false
    }
  },
  mounted() {
    this.setScrollY()
    window.addEventListener('scroll', this.setScrollY)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.setScrollY)
  },
  methods: {
    setScrollY() {
      this.scrollY = window.scrollY
    },
    onClickOutside() {
      this.open = false
    }
  }
}
